@charset "utf-8";

html,
body,
div,
p,
span,
strong,
b,
em,
iframe,
pre,
h1,
h2,
h3,
h4,
h5,
h6,
img,
dl,
dt,
dd,
fieldset,
form,
legend,
label,
table,
caption,
thead,
tbody,
tfoot,
tr,
th,
td,
ul,
ol,
li,
a,
input,
select,
textarea {
  margin: 0;
  padding: 0;
  border: 0 none;
}

ul,
ol,
li {
  list-style: none;
}
em,
address {
  font-style: normal;
}
img {
  border: 0 none;
  font-size: 0;
  line-height: 0;
}
sup {
  position: relative;
  top: 2px;
  font-size: 11px;
  line-height: 100%;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption {
  overflow: hidden;
  width: 0;
  height: 0;
  font-size: 0;
  line-height: 0;
}
th,
td {
  vertical-align: middle;
}

a {
  color: #333;
  text-decoration: none;
  line-height: 1;
}
a:hover,
a:focus,
a:active {
  text-decoration: underline;
}
a:visited {
  color: #333;
}
*,
html {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
